function slider({container, slide, nextArrow, prevArrow, totalCounter, currentCounter, wrapper, field}) { //destructuring assignment
  //Slider
  const slider = document.querySelector(container),
    slidesWrapper = slider.querySelector(wrapper),
    slidesField = slidesWrapper.querySelector(field),
    slides = slidesField.querySelectorAll(slide),
    prev = slider.querySelector(prevArrow),
    next = slider.querySelector(nextArrow),
    totalElem = slider.querySelector(totalCounter),
    currentElem = slider.querySelector(currentCounter),
    width = window.getComputedStyle(slidesWrapper).width;

  let slidesIndex = 1;
  let offset = 0;

  slidesField.style.width = 100 * slides.length + '%';
  slidesField.style.display = 'flex';
  slidesField.style.transition = '0.5s all';

  slidesWrapper.style.overflow = 'hidden';

  slides.forEach(slide => {
    slide.style.width = width;
  });

  slider.style.position = 'relative';

  const indicator = document.createElement('ol'),
    dots = [];
  indicator.classList.add('carousel-indicators');
  slider.append(indicator);

  for (let i = 0; i < slides.length; i++) {
    const dot = document.createElement('li');
    dot.classList.add('dot');
    dot.setAttribute('data-slide-to', i + 1);
    if (i === 0) {
      dot.style.opacity = 1;
    }
    indicator.append(dot);
    dots.push(dot);
  }

  function getTimerZero() {
    if (slidesIndex < 10) {
      totalElem.textContent = `0${slides.length}`;
      currentElem.textContent = `0${slidesIndex}`;
    } else {
      totalElem.textContent = slides.length;
      currentElem.textContent = slidesIndex;
    }
  }

  getTimerZero();

  function changeOpacity() {
    dots.forEach(dot => {
      dot.style.opacity = '.5';
    });
    dots[slidesIndex - 1].style.opacity = 1;
  }

  function deleteNotDigits(str) {
    return +str.replace(/\D/ig, '');
  }

  next.addEventListener('click', () => {
    if (offset == (deleteNotDigits(width) * (slides.length - 1))) {
      offset = 0;
    } else {
      offset += deleteNotDigits(width);
    }

    slidesField.style.transform = `translateX(-${offset}px)`;

    if (slidesIndex == slides.length) {
      slidesIndex = 1;
    } else {
      slidesIndex++;
    }

    getTimerZero();
    changeOpacity();
  });

  prev.addEventListener('click', () => {
    if (offset == 0) {
      offset = deleteNotDigits(width) * (slides.length - 1);
    } else {
      offset -= deleteNotDigits(width);
    }

    slidesField.style.transform = `translateX(-${offset}px)`;

    if (slidesIndex == 1) {
      slidesIndex = slides.length;
    } else {
      slidesIndex--;
    }

    getTimerZero();
    changeOpacity();
  });

  dots.forEach(dot => {
    dot.addEventListener('click', (e) => {
      const slideTo = e.target.getAttribute('data-slide-to');
      slidesIndex = slideTo;

      offset = deleteNotDigits(width) * (slideTo - 1);
      slidesField.style.transform = `translateX(-${offset}px)`;

      getTimerZero();
      changeOpacity();
    });
  });
}

export default slider;